.box {
    max-width: 1366px;
    margin: 0 auto;
    min-height: 707px;
    border-radius: 13px;
    margin-top: 10px;
    display: flex;
    background: #B129D7;
}

.topBox {
    height: 102px;
    border-radius: 0 13px 0 0;
    padding-left: 25px;
    display: flex;
    align-items: center;
}

.insideBox {
    position: sticky;
    top: 0;
}

.name, .info {
    font-family: 'Be Vietnam Pro', sans-serif;
    font-size: 18px;
    font-weight: 600;
    margin: 0;
    text-align: left;
    color: #fff;
}

.info {
    font-weight: 400;
}

.stick {
    position: sticky;
    top: 0;
    -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
    -moz-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
    box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
    background: #B129D7;
}

.left {
    width: 400px;
    height: 707px;
    border-radius: 13px 0 0 13px;
    overflow: scroll;
    background: #B129D7;
}

.right {
    width: 966px;
    height: 707px;
    border-radius: 0 13px 13px 0;
    overflow: scroll;
    background: #B129D7;
    display: flex;
    flex-direction: column;
}

.send {
    height: 77px;
    position: sticky;
    bottom: 0;
    background: #B129D7;
}

.flex {
    display: flex;
    align-items: center;
    padding-left: 15px;
}

.form input {
    border-radius: 13px;
    margin: 0;
}

.form input::placeholder {
    font-family: 'Be Vietnam Pro', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #000;
}

.form button {
    min-width: 50px;
    height: 77px;
    cursor: pointer;
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    transform: rotate(90deg);
    margin: 0 15px 0 15px;
}
.messageReq {
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Be Vietnam Pro', sans-serif;
}
.addMessageInput {
    width: 850px;
}
.requiredBox {
    padding-top: 5px;
    position: absolute;
}

@media (max-width: 840px) {
    .left {
        display: none;
    }
    .right {
        border-radius: 13px
    }
  }